<template>
    <div id="show_item">
        <!-- <el-table :data="showMessages" height="210">
            <el-table-column prop="Message" label="vpc network status"></el-table-column>
        </el-table> -->
        <!-- <div v-for="(item, index) in showMessages" :key="index" class="showBody">{{item.Message}}</div> -->
        <!-- <p v-for="(item, index) in showMessages" :key="index">{{item}}</p> -->
        <div v-if="!isMessagesEmpty">
            <el-card v-for="(item, index) in showMessages" :key="index">{{item.Message}}</el-card>
        </div>
        <div v-else>
            <el-card>""</el-card>
        </div>
    </div>
</template>

<script>
import {mapState} from "vuex"
export default {
    computed: {
        ...mapState(["showMessages"]),
        isMessagesEmpty() {
            if (this.showMessages.length === 0) {
                return true;
            } else {
                return false;
            }
        }
    },
    mounted: function() {
        this.$store.dispatch("getStatus");
    }
    // watch: {
    //     showMessages: {
    //         handler() {
    //             this.$nextTick();
    //         },
    //         deep: true
    //     }
    // }
}
</script>

