    <div>
        <vue-terminal :command-list="commandList" :task-list="taskList"></vue-terminal>
    </div>
</template>
<script>
import VueTerminal from 'vue-terminal'
export default {
    components:{
        VueTerminal
    },
    data() {
        return {
            // taskList: {
