            //         description: 'Echoes input',
            //         echo: function(pushToList, input) {
            //             input = input.split(' ')
            //             input.splice(0, 1)
            //             const p = new Promise((resolve, reject) => {
            //                 pushToList({ message: input[0] });
            //                 resolve({ type: 'success', message: "over!" })
            //             })
            //             return p
            //         },
            //         assist: this.thisis
            //     },
            //     subnet: {
            //         description: "Subnet manipulation",
            //         subnet(pushToList, input) {
            //             input = input.split(' ');
            //             input.splice(0, 1);
            //             console.log(this);
            //             if (input[2] === "all") {
            //                 const p  = new Promise((resolve, reject) => {
            //                     console.log(this);
            //                     resolve({message: "joe"})
            //                 }); 
            //                 return p;
            //             } else {
            //                 const p  = new Promise((resolve, reject) => {
            //                     console.log(this);
            //                     resolve({message: "no"})
            //                 });
            //                 return p;
            //             }
            //         }
            //     }
            // },
            commandList: {
                joe: {
                    description: "this is joe",
                    messages: [
                        {
                            message: "joejoejoe"
                        }
                    ]
                }
            }
        }
    },
    methods: {
        echo: function(pushToList, input) {
            input = input.split(' ')
            input.splice(0, 1);
            const p = new Promise((resolve, reject) => {
                pushToList({ message: input[0] });
                resolve({ type: 'success', message: "over!" })
            })
            return p
        },
        subnet: function(pushToList, input) {
            input = input.split(' ');
            input.splice(0, 1);
            if (input[1] === "all" && input[0] == "ip" && input.length === 2) {
                const p = new Promise((resolve, reject) => {
                    // console.log("ipall");
                    // console.log(this.api);
                    this.api.request.get("/status").then((res) => {
                        console.log(res);
                        if (res.data.showMessages) {
                            res.data.showMessages.forEach(p => {
                                pushToList({message: p.Message});
                            });
                        }
                        resolve({message: "done"});
                    }).catch((err) => {
                        reject({message: "error!"});
                    })
                    // pushToList({message: "ip all"});
                    // pushToList({message: "ip---1"});
                    // resolve({message: "ip all done"});
                })
                return p;
            } else if (input[1] === "set") {
                const p = new Promise((resolve, reject) => {
                    if (input.length !== 4) {
                        //subnet ip set vm_name ip
                        reject({message: "Type 'help' to get help"});
                    } else {
                        // console.log("ip change");
                        // console.log(this.api);
                        
                        this.api.request.post("/VM/revise", JSON.stringify({
                            "id": input[2],
                            "ip": input[3]
                        })).then((res) => {
                            console.log(res)
                            if (res.data.success === false) {
                                reject({message: "error!"});
                            }
                            this.$store.dispatch("getStatus");
                            resolve({message: "ip set done"});
                        }).catch((err) => {
                            reject({message: "error!"});
                        })
                        // resolve({message: `ip set ${input[2]} ${input[3]}`});
                    }
                })
                return p;
            } else {
                const p = new Promise((resolve, reject) => {
                    reject({message: "Type 'help' to get help"});
                })
                return p;
            }
        }
    },
    computed: {
        taskList() {
            let a = {
                echo: {
                    description: "repeat aself"
                },
                subnet: {
                    description: "Subnet manipulationa"
                }
            }
            a.echo.echo = this.echo;
            a.subnet.subnet = this.subnet;
            return a;
        }
    }
}
