<template>
    <div id="newNewCon">
        <div class="navbar navbar-fixed-top">
            <div class="navbar-inner">
                <div class="container-fluid">
                    <a class="btn btn-navbar" data-toggle="collapse" data-target=".nav-collapse"> <span class="icon-bar"></span>
                     <span class="icon-bar"></span>
                     <span class="icon-bar"></span>
                    </a>
                    <a class="brand" href="#">Sdn Subnet System</a>
                    <div class="nav-collapse collapse">
                        <ul class="nav pull-right">
                            <li class="dropdown">
                                <a href="#" role="button" class="dropdown-toggle" data-toggle="dropdown"> 
                                <!-- <i class="icon-envelope"></i> Vincent Gabriel <i class="caret"></i> -->
                                <i class="icon-envelope"></i> Contact
                                <!-- <i class="icon-envelope"></i>
                                <i class="icon-star-empty"></i>
                                <i class="icon-tags"></i> -->
                                <i class="icon-bell"></i> Notification
                                <i class="icon-th"></i>
                                </a>
                            </li>
                        </ul>
                    </div>
                    <!--/.nav-collapse -->
                </div>
            </div>
        </div>
        <div class="container-fluid">
            <div class="row-fluid">
                <div class="span3" id="sidebar">
                    <el-card>
                        <div class="search">
                            <el-input suffix-icon="el-icon-search" size="medium" class="search-input" v-model="searchContent"></el-input>
                        </div>
                        <br>
                        <!-- <select v-model="user">
                            <option value="user1">user1</option>
                            <option value="user2">user2</option>
                        </select> -->
                        <el-collapse v-model="user" accordion>
                            <el-collapse-item name="user1">
                                <template slot="title">
                                    <div id="user1center">user1</div>
                                </template>
                                <div id="user1btn">
                                    <el-button @click="createDia(1)">VPC</el-button><br>
                                    <el-button @click="createDia(2)"></el-button>
                                </div>
                            </el-collapse-item> 
                            <el-collapse-item title="user2" name="user2">
                                <div id="user2btn">
                                    <el-button @click="createDia(1)">VPC</el-button><br>
                                    <el-button @click="createDia(2)"></el-button>
                                </div>
                            </el-collapse-item>
                        </el-collapse>
                        <p> </p>
                        <div id="adminBtn">
                            <el-button @click="switchAdmin"><i class="el-icon-user">admin</i></el-button>
                        </div>
                        <!-- <el-input v-model="vm_num"></el-input>
                        <el-checkbox v-model="info.user1">user1</el-checkbox>
                        <el-checkbox v-model="info.user2">user2</el-checkbox>
                        <el-input v-model="info.num1"></el-input>
                        <el-input v-model="info.num2"></el-input>
                        <el-input v-model="info.router[0]"></el-input>
                        <el-input v-model="info.router[1]"></el-input>
                        <el-input v-model="info.router[2]"></el-input>
                        <el-input v-model="info.router[3]"></el-input> -->
                        <!-- <p>{{vm_num}}</p> -->
                        <br>
                    </el-card>
                </div>
                
                <!--/span-->
                <div class="span9" id="content3" v-if="isUser">
                    <div class="row-fluid">
                        <div class="span6">
                            <!-- block -->
                            <div class="block" id="marginNo">
                                <!-- content1 for graph -->
                                <el-card :body-style="{padding: '0px'}"><graph1 :num="currentNum"></graph1></el-card>
                                
                            </div>
                            <!-- /block -->
                        </div>
                        <div class="span6">
                            <!-- block -->
                            <div class="block" id="marginNo1">
                                <!-- content1 for graph -->
                                <el-card :body-style="{padding: '0px'}"><graph2 :num="currentNum"></graph2></el-card>
                            </div>
                            <!-- /block -->
                        </div>
                    </div>
                    <div class="row-fluid">
                        <div class="span6">
                            <!-- block -->
                            <div class="block" id="showBlock">
                                <!-- content2 for show -->
                                <el-card :body-style="{padding: '2px'}"><show_item></show_item></el-card>
                            </div>
                            <!-- /block -->
                        </div>
                        <div class="span6">
                            <!-- block -->
                            <div class="block">
                                <!-- content3 for terminal -->
                                <terminal_con></terminal_con>
                                <!-- <new_ter></new_ter> -->
                            </div>
                            <!-- /block -->
                        </div>
                    </div>
                </div>
                <div class="span9" id="content23" v-else>
                    <div class="row-fulid">
                        <div class="span12">
                            <div class="block" id="marginNo2">
                                <el-card :body-style="{padding: '0px'}"><graph3 :info="newInfo"></graph3></el-card>
                            </div>
                        </div>
                    </div>
                    <div class="row-fulid">
                        <div class="span12">
                            <div class="block">
                                <el-card :body-style="{padding: '0px'}"><graph4></graph4></el-card>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <el-dialog
            title="VPC"
            :visible.sync="VPCDialogVisible"
            width="40%" class="Dialog">
            <el-tag effect="plain">user: {{user}}</el-tag>
            <p> </p>
            <p></p> <el-select v-model="subnet"><el-option v-for="(item, index) in subnets" :key="item" :value="index" :label="item">{{item}}</el-option></el-select>
            <p> </p>
            <el-button @click="createDia(3)"></el-button>
        </el-dialog>
        <el-dialog
            title=""
            :visible.sync="VMDialogVisible"
            width="40%" class="Dialog">
            <el-tag effect="plain">user: {{user}}</el-tag>
            <p> </p>
            <el-tag effect="plain">region: default</el-tag>
            <p> </p>
            <p></p> 
            <!-- <el-select v-model="vm_num"><el-option value="1">1</el-option><el-option value="2">2</el-option><el-option value="3">3</el-option></el-select> -->
            <el-input-number v-model="vm_num" :controls="true" :min="1"></el-input-number>
            <p> </p>
            <el-button @click="createDia(4)"></el-button>
        </el-dialog>
    </div>
</template>

<script>
import terminal_con from "./terminal"
import show_item from "./show_item"
import graph1 from "./graph1"
import graph2 from "./graph2"
import graph3 from "../admin/graph3"
import graph4 from "../admin/graph4"
export default {
    components: {
        terminal_con,
        show_item,
        graph1,
        graph2,
        graph3,
        graph4
    },
    data() {
        return {
            user: "",
            VPCDialogVisible: false,
            VMDialogVisible: false,
            subnet: "",
            searchContent: "",
            subnets: [
                "Default: 10.0.1.1/24",
                "Type A: 10.0.0.1/24",
                "Type C: 192.168.1.1/24"
            ],
            user1VmNum: 0,
            user2VmNum: 0,
            info: {
                user1: true,
                user2: true,
                num1: 2,
                num2: 3,
                router: [1, 1, 1, 1]
            },
            newInfo: {
                user1: false,
                user2: false,
                num1: {
                    area1: 0,
                    area2: 0
                },
                num2: {
                    area1: 0,
                    area2: 0
                },
                router: [0, 0, 0, 0]
            },
            createdVPC: [0, 0],
            vm_num: 0
        }
    },
    methods: {
        createDia(val) {
            //
            if (val === 1) {
                this.subnet = "";
                this.VPCDialogVisible = true;
            } else if (val === 2) {
                this.VMDialogVisible = true;
            } else if (val === 3) {
                if (this.subnet === "") {
                    this.$message.error('');
                } else {
                    let subnet;
                    if (this.subnet == 0 || this.subnet == 1) {
                        subnet = 1;
                    } else if (this.subnet == 2) {
                        subnet = 3;
                    }
                    this.createVPC({
                        user: this.user,
                        type: subnet
                    });
                    this.VPCDialogVisible = false;
                }
            } else {
                let that = this;
                this.api.request.get("status/VPC").then(function (response) {
                    that.createdVPC[0] = response.data[0];
                    that.createdVPC[1] = response.data[1];
                    // console.log(response)
                    if ((that.user === "user1" && that.createdVPC[0]) || (that.user === "user2" && that.createdVPC[1])) {
                        let subnet;
                        if (that.subnet == 0 || that.subnet == 1) {
                            subnet = 1;
                        } else if (that.subnet == 2) {
                            subnet = 3;
                        }
                        that.createVM({
                            user: that.user,
                            type: subnet,
                            num: that.vm_num
                        })
                        that.VMDialogVisible = false;
                        that.updateVMNum();
                        that.updateMessage();
                    } else {
                        that.$message.error('VPC');
                    }
                })
            }
        },
        createVPC(param) {
            //VPC
            this.$store.dispatch('createVPC', param);
        },
        createVM(param) {
            //VM
            this.$store.dispatch('createVM', param);
        },
        switchAdmin() {
            //admin
            this.user = "admin";
            this.$store.dispatch("getStatus");
            let that = this
            this.api.request.get("/status/info").then(function (response) {
                // console.log(response)
                that.newInfo = response.data;
            })
            this.getCreatedVPC();
        },
        getCreatedVPC() {
            //vpc
            let that = this;
            this.api.request.get("status/VPC").then(function (response) {
                // console.log("joe")
                // console.log(this);
                that.createdVPC[0] = response.data[0];
                that.createdVPC[1] = response.data[1];
            })
        },
        updateVMNum() {
            let that = this;
            this.api.request.get("/status/info").then(function (response) {
                that.user1VmNum = response.data.num1.area1 + response.data.num1.area2;
                that.user2VmNum = response.data.num2.area1 + response.data.num2.area2;
            })
        },
        updateMessage() {
            // this.$store.dispatch("updateMessage");
            this.$store.dispatch("getStatus");
        }
    },
    computed: {
        isUser() {
            if (this.user === "admin") {
                return false;
            } else {
                return true;
            }
        },
        currentNum() {
            if (this.user === "" || this.user === "user1") {
                return this.user1VmNum;
            } else if (this.user === "user2") {
                return this.user2VmNum;
            }
        }
    },
    mounted: function() {
        this.updateVMNum();
        this.updateMessage();
    }
}
</script>

<style scoped>
.search {
    display: flex;
}
.search-input {
    width: 200px;
}
.el-icon-search {
    float: right;
}
.Dialog {
    text-align: center;
}
.el-button {
    width: 200px;
}
/* #user1btn, #user2btn, #adminBtn, #user1center{
    text-align: center;
} */
#showBlock {
    height: 210px;
    max-height: 210px;
    overflow-y: scroll;
}
.el-tag {
    width: 20%;
}
</style>