import api from "../ax"

export default {
    createVPC ({commit}, param) {
        // let a = {
        //     user: param.user,
        //     type: param.type
        // }
        api.request.post("/VPC", JSON.stringify({
            user: param.user,
            type: param.type,
            userType: param.user
        }))
        // api.request.post("/VPC", JSON.stringify(a));
    },
    createVM ({commit}, param) {
        api.request.post("/VM", JSON.stringify({
            user: param.user,
            type: param.type,
            num: param.num,
            userType: param.user
        }))
    },
    getStatus ({commit}) {
        api.request.get("/status").then(function(response) {
            // console.log(response);
            console.log("rerere!")
            console.log(response)
            commit("getStatus", response);
        }).catch(function (err) {
            console.log(err);
        })
    }
}