export default {
    getStatus(state, param) {
        // console.log(param.data)
        // state.
        state.showMessages = [];
        if (param.data.showMessages) {
            for (let i = 0; i < param.data.showMessages.length; i++) {
                state.showMessages.push({
                    Message: param.data.showMessages[i].Message
                })
            }
        }
        console.log(state)
    }
}