                    node.set("data", data);
                    // node.image = Q.getAllImages()[4];
                    // node.image = "switcher.svg";
                    if (data.type == "router") {
                        node.image = 'router.svg';
                    } else if (data.type == 'vm') {
                        node.image = "vm.svg";
                    } else if (data.type == "firewall") {
                        node.image = "firewall.svg";
                    }
