                    // }
                    map[data.id] = node;
                });
            } else {
                console.log("nonono")
            }
            if(json.edges){
                Q.forEach(json.edges, function(data){
                    var from = map[data.from];
                    var to = map[data.to];
                    if(!from || !to){
                        return;
                    }
                    var edge = graph.createEdge(data.name, from, to);
                    edge.setStyle(Q.Styles.ARROW_TO, false);
                    edge.set("data", data);
                }, graph);
            }
