            //     ],
            //     "edges": [
            //         {"name": "Edge", "from":1, "to":4},
            //         {"name": "Edge", "from":2, "to":4},
            //         {"name": "Edge", "from":3, "to":4},
            //         {"name": "Edge", "from":4, "to":5},
            //     ]
            // }
            class node {
                constructor (name, id, type, x, y) {
                    this.name = name;
                    this.id = id;
                    this.type = type;
                    this.x = x;
                    this.y = y;
                } 
            }
            class edge {
                constructor(name, from, to) {
                    this.name = name;
                    this.from = from;
                    this.to = to;
                }
            }
            let a = {
                nodes: [],
                edges: []
            }
            a.nodes.push(new node("vpc router", 1, "router", 0, 210));
            a.nodes.push(new node("firewall", 2, "firewall", 200, 210));
            a.edges.push(new edge("edge", 1, 2));
            let num = parseInt(this.num);
            for (let i = 0; i < num; i++) {
                let radius = (((2 * 3.14) / (num + 1)) * (i + 1));
                let relativeX = Math.cos(radius) * 200;
                let relativeY = Math.sin(radius) * 200;
                a.nodes.push(new node("vm" + (i + 1), 3 + i, "vm", 0 + relativeX, 210 + relativeY));
                a.edges.push(new edge("edge", 3 + i, 1));
            }

            // this.translateToQuneeElements(JsonA, graph);
            // console.log(a)
            // console.log(JsonA)
            this.translateToQuneeElements(a, graph);
            // var layouter = new Q.SpringLayouter(graph);
            // var layouter = new Q.TreeLayouter(graph);
            // var layouter = new Q.BalloonLayouter(graph);
            // layouter.start();
        }
    },
    mounted: function() {
        this.draw();
    },
    watch: {
        num() {
            let root = document.getElementById("root");
            root.removeChild(root.childNodes[0]);
            let newCanvas = document.createElement("div");
            newCanvas.setAttribute("id", "canvas");
            newCanvas.style.height = "400px";
            root.appendChild(newCanvas);
            this.draw();
        }
    }
}
</script>

<style scoped>
div {
    height: 400px;
}
</style>