    props: ["info"],
    methods: {
        translateToQuneeElements(json, graph){

            graph.zoomToOverview();

            var map = {};
            if(json.nodes){
                Q.forEach(json.nodes, function(data){
                    var node = graph.createNode(data.name, data.x || 0, data.y || 0);
                    node.set("data", data);
                    // node.image = Q.getAllImages()[4];
                    // node.image = "switcher.svg";
                    if (data.type == "router") {
                        node.image = 'router.svg';
                    } else if (data.type == 'vm') {
                        node.image = "vm.svg";
                    } else if (data.type == "firewall") {
                        node.image = "firewall.svg";
                    }
                    // switch (data.user) {
                    //     case "user1":
                    //         node.setStyle(Q.Styles.IMAGE_BACKGROUND_COLOR, "blue");
                    //         break;
                    //     case "user2":
                    //         node.setStyle(Q.Styles.IMAGE_BACKGROUND_COLOR, "yellow");
                    //         break;
                    //     case "admin":
                    //         node.setStyle(Q.Styles.IMAGE_BACKGROUND_COLOR, "red")
                    // }
                    map[data.id] = node;
                });
            }
            if(json.edges){
                Q.forEach(json.edges, function(data){
                    var from = map[data.from];
                    var to = map[data.to];
                    if(!from || !to){
                        return;
                    }
                    var edge = graph.createEdge(data.name, from, to);
                    edge.setStyle(Q.Styles.ARROW_TO, false);
                    edge.set("data", data);
                }, graph);
            }
        },
        change(a) {

            var VPNFlexEdgeUI = function(edge, graph){
                Q.doSuperConstructor(this, VPNFlexEdgeUI, arguments);
            }
            VPNFlexEdgeUI.prototype = {
                drawEdge: function(path, fromUI, toUI, edgeType, fromBounds, toBounds){
                    var from = fromBounds.center;
                    path.curveTo(from.x, from.y, internet.x, internet.y);
                }
            }
            Q.extend(VPNFlexEdgeUI, Q.EdgeUI);

            var graph = new Q.Graph(document.getElementById("canvas3"));
            // graph.canvas.style.backgroundColor = '#FFD';
            // console.log(graph)
            // graph.canvasPanel.style.backgroundColor = "#DDFBEF"
            graph.zoomToOverview();
            graph.styles = {};
            graph.styles[Q.Styles.LABEL_FONT_SIZE] = 18;


            var SVGIcons = {
