            },
                "point.svg": {
 draw: function(ctx){
ctx.save();
ctx.strokeStyle="rgba(0,0,0,0)";
ctx.miterLimit=4;
ctx.font="15px / 21.4286px ''";
ctx.font="   15px ";
ctx.scale(0.4,0.4);
ctx.scale(0.1953125,0.1953125);
ctx.save();
ctx.font="   15px ";
ctx.beginPath();
ctx.moveTo(593.180416,494.907717);
ctx.bezierCurveTo(593.180416,523.161201,570.2859860000001,546.073027,542.022269,546.073027);
ctx.bezierCurveTo(513.7616220000001,546.073027,490.8497960000001,523.161201,490.8497960000001,494.90771700000005);
ctx.bezierCurveTo(490.8497960000001,466.63786000000005,513.7616220000001,443.74240700000007,542.022269,443.74240700000007);
ctx.bezierCurveTo(570.285986,443.742407,593.180416,466.63786,593.180416,494.907717);
ctx.closePath();
ctx.fill();
ctx.stroke();
ctx.restore();
ctx.restore();
}

}
            }
            for (var name in SVGIcons) {
                Q.registerImage(name, SVGIcons[name]);
            }


            function createNode(image, x, y, name, group){
                var node = graph.createNode(name, x, y);
                if(image == "pc"){
                    node.image = Q.getAllImages()[0];
                } else if (image === "router") {
                    node.image = Q.getAllImages()[2];
                } else if (image == "firewall") {
                    node.image = 'firewall.svg'
                } else if (image == "point") {
                    node.image = "point.svg"
                }
                if(group){
                    group.addChild(node);
                }
                return node;
            }

            function createText(name, x, y, fontSize, color, parent){
                var text = graph.createText(name, x, y);
                text.setStyle(Q.Styles.LABEL_ANCHOR_POSITION, Q.Position.CENTER_MIDDLE);
                text.setStyle(Q.Styles.LABEL_POSITION, Q.Position.CENTER_MIDDLE);
                text.setStyle(Q.Styles.LABEL_FONT_SIZE, fontSize);
                text.setStyle(Q.Styles.LABEL_COLOR, color);
                text.setStyle(Q.Styles.LABEL_BACKGROUND_COLOR, null);
                if(parent){
                    parent.addChild(text);
                }
                return text;
            }

            function createGroup(padding){
                var group = graph.createGroup();
                // group.groupImage = Q.Graphs.group_cloud;
                group.padding = padding || 30;
                return group;
            }

            function createEdge(a, b, color, dashed, name){
                var edge = graph.createEdge(name, a, b);
                if(dashed){
                    edge.setStyle(Q.Styles.EDGE_LINE_DASH, [8, 5]);
                }
                edge.setStyle(Q.Styles.EDGE_WIDTH, 3);
                edge.setStyle(Q.Styles.EDGE_COLOR, color);
                edge.setStyle(Q.Styles.ARROW_TO, false);
                return edge;
            }

            var VPNFlexEdgeUI = function(edge, graph){
                Q.doSuperConstructor(this, VPNFlexEdgeUI, arguments);
            }
            VPNFlexEdgeUI.prototype = {
                drawEdge: function(path, fromUI, toUI, edgeType, fromBounds, toBounds){
                    var from = fromBounds.center;
                    path.curveTo(from.x, from.y, internet.x, internet.y);
                }
            }
            
            let lines = [];

            let ownColor = {
                // #EBFFEF #97B3D0FF #FE96FB
                group: "#F1EEFD",
                normline: "#4863B7",
                sepcialline: "#B00F20"
            }

            //
            Q.extend(VPNFlexEdgeUI, Q.EdgeUI);

            var group = createGroup(50);
            group.setStyle(Q.Styles.RENDER_COLOR, ownColor.group)
            let leftGroup = group;

            let firewall = createNode("firewall", 50, 250, "firewall", group);
            let leftOVS = createNode("router", 330, 250, "OVS", group)

            if (a.user1) {
                var router1 = createNode("router", 200, 150, "user1-VPC-Router", group);
                createEdge(firewall, router1, ownColor.normline, true);
                createEdge(leftOVS, router1, ownColor.normline, true);
            } else {
                a.router[0] = 0;
                a.router[1] = 0;
            }
            if (a.user2) {
                var router2 = createNode("router", 200, 350, "user2-VPC-Router", group);
                createEdge(firewall, router2, ownColor.normline, true);
                createEdge(leftOVS, router2, ownColor.normline, true);
            } else {
                a.router[2] = 0; 
                a.router[3] = 0; 
            }

            var internet = createNode(Q.Graphs.group_cloud, 600, 250, "");
            internet.image = Q.getAllImages()[1];
            internet.size = {width: 100};

            createEdge(leftOVS, internet, ownColor.normline)

            // 4B878BFF C4E1FF 9ED9CCFF ABD1C9FF #6d7d92 e5e2f1
            var n = createSubGroup(800, -20, ownColor.group, a.num1.area1 + a.num2.area1, a.num1.area1, a.num2.area1);
            var o = createSubGroup(800, 350, ownColor.group, a.num1.area2 + a.num2.area2, a.num1.area2, a.num2.area2);
            // console.log(n.group);
            createText("", n.router.x, n.router.y - 80, 20, "black", n.group);
            createText("", o.router.x - 10, o.router.y + 80, 20, "black", o.group);
            createText("", group.x - 20, group.y + 190, 20, "black", group);
            
            // 97B3D0FF
            let z1 = createEdge(internet, n.router),
                z2 = createEdge(internet, o.router);
            z1.edgeType = Q.Consts.EDGE_TYPE_ZIGZAG;
            z2.edgeType = Q.Consts.EDGE_TYPE_ZIGZAG;
            z1.setStyle(Q.Styles.EDGE_COLOR, ownColor.normline);
            z2.setStyle(Q.Styles.EDGE_COLOR, ownColor.normline)
            // createEdge(internet, n.router).edgeType = Q.Consts.EDGE_TYPE_ZIGZAG;
            // createEdge(internet, o.router).edgeType = Q.Consts.EDGE_TYPE_ZIGZAG;
            if (a.router[0]) {
                // #ADEFD1FF #FF79BC #603F83FF EEA47FFF E94B3CFF 2BAE66FF F93822FF
                lines.push(createEdge(router1, n.router, ownColor.sepcialline, true));
            }
            if (a.router[1]) {
                lines.push(createEdge(router1, o.router, ownColor.sepcialline, true));
            }
            if (a.router[2]) {
                lines.push(createEdge(router2, n.router, ownColor.sepcialline, true));
            }
            if (a.router[3]) {
                lines.push(createEdge(router2, o.router, ownColor.sepcialline, true));
            }
            
            lines.map((item) => {
                // item.edgeType = Q.Consts.EDGE_TYPE_ELBON;
                // item.setStyle(Q.Styles.EDGE_LINE_DASH, [8, 4, 0.01, 4]);
                // item.setStyle(Q.Styles.LINE_CAP, "round");
                item.uiClass = VPNFlexEdgeUI;
            })
            // createEdge(leftGroup, router2, "#FF79BC", true)

            

            function createSubGroup(x, y, renderColor, n, user1SingAreaNum, user2SingAreaNum){
            // function createSubGroup(x, y, renderColor, n){
                var group = createGroup();
                if(renderColor){
                    group.setStyle(Q.Styles.RENDER_COLOR, renderColor);
                }
                var a = createNode("router", 49 + x, 100 + y, 'OVS', group);
                let vmArr = [];
                vmArr.push(createNode("pc", 150 + x, 50 + y, 'VM', group));
                vmArr.push(createNode("pc", 150 + x, 150 + y, 'VM', group));
                vmArr.push(createNode("pc", 300 + x, 50 + y, 'VM', group));
                vmArr.push(createNode("pc", 300 + x, 150 + y, 'VM', group));
                // console.log(vmArr[1]);
                for (let i = 0; i < user1SingAreaNum; i++) {
                    vmArr[i].$name = "VM-user1";
                }
                for (let i = 0; i < user2SingAreaNum; i++) {
                    vmArr[i+user1SingAreaNum].$name = "VM-user2";
                }
                for (let i = 0; i < n; i++) {
                    createEdge(a, vmArr[i], ownColor.normline);
                }
                let ret = {
                    router: a,
                    group: group
                }
                return ret;
            }

            // let show1 = createNode("point", 1300, 400);
            // let show2 = createNode("point", 1500, 400);
            // let show3 = createNode("point", 1300, 300);
            // let show4 = createNode("point", 1500, 300);
            // createEdge(show1, show2, "black", true, "");
            // createEdge(show3, show4, "black", false, "");
        }
    },
    mounted: function() {
        let a = {
            user1: true,
            user2: true,
            num1: 2,
            num2: 3,
            router: [0, 1, 1, 0]
        }
        this.change(this.info);
    },
    watch: {
        info: {
            handler() {
                // console.log("chane")
                let root = document.getElementById("root3");
                root.removeChild(root.childNodes[0]);
                let newCanvas = document.createElement("div");
                newCanvas.setAttribute("id", "canvas3");
                newCanvas.style.height = "calc((100vh - 40px) / 2)";
                root.appendChild(newCanvas);
                this.change(this.info);
            },
            deep: true
        }
    }
}
</script>

<style scoped>
div {
    /* height: 400px; */
    height: calc((100vh - 40px) / 2);
}
</style>